    .circle {
      display: flex;
      position: absolute;
      z-index: 999;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      border-radius: 50%;
      border: 1px solid #454545;
      font-weight: normal;
      background: white;
    }

